
const createPopup = (url: string, width: number = 450, height: number = 600): void => {

    /*
     * 2023-03-25: If we display the URL in a popup then we have to handle
     * the popup closing after the extension is closed. For the time being
     * I think it's most approrpiate to just open the URL in a new tab.
     * Opening the URL in a new tab will automatically close the extension
     * ensuring that the state is reset upon successful login.
     *
     * If we can figure out how to automatically close the popup after the
     * authentication is complete then we can change this to open the URL
     * in a popup.
     *
     * // const left: number = (screen.width / 2) - (width / 2);
     * // const top: number = (screen.height / 2) - (height / 2);
     * //
     * // const data: chrome.windows.CreateData = { url, type: 'normal', width, height, left, top };
     * //
     * // chrome.windows.create(data).then((window: chrome.windows.Window) => { });
     */

    window.open(url, '_blank');
};

export { createPopup };
