import './Results.scss';

import { Component } from 'react';
import { QuizletSet } from '../../types/quizlet-set';
import { Promotion, RandomizedPromotion } from '../../types/promotion';
import { Settings } from '../../types/settings';
import { ReCAPTCHAData } from '../../types/re-CAPTCHA';

type ResultComponentProperties = {
    reCAPTCHAData: ReCAPTCHAData | undefined;
    results: Array<QuizletSet>;
    randomizedPromotion: RandomizedPromotion | undefined;
    settings: Settings;
    isLoading: boolean;
    onClearClick: (resetInput: boolean) => void;
    onSettingsClick: (displayGold?: boolean) => void;
    onWebsiteClick: (index: number) => void;
    onPromotionClick: (id: number) => void;
    onReCAPTCHAResult: (value: any) => void;
    noResults: boolean;
    question: string;
};

export default class Results extends Component<ResultComponentProperties, { captchaLoaded: boolean }> {

    public reCAPTCHAIframe: HTMLIFrameElement | undefined;

    constructor(props: any) {

        super(props);
        this.state = { captchaLoaded: false };
    }

    public onVisitWebsiteClick(result: QuizletSet, index: number): void {
        this.props.onWebsiteClick(index);
        window.open(result.url.concat(`#:~:text=${encodeURI(result.question)}`), '_blank');
    }

    public onVisitPromotionalWebsite(promotion: Promotion): void {

        this.props.onPromotionClick(promotion.id);

        if (promotion.goldPromotion === true) {
            this.props.onSettingsClick(true);
            return;
        }

        window.open(promotion.url, '_blank');
    }

    public onTrySearchingWithGoogleClick(): void {
        window.open(`http://google.com/search?q=${this.props.question} site:quizlet.com`, '__blank');
    }

    public onReportAProblemClick(): void {
        window.open(`https://www.quizzardapp.com/feedback`, '__blank');
    }

    public onReCAPTCHAManualFix(): void {
        this.props.onClearClick(false);
        window.open(`http://google.com/search?q=${this.props.question} quizlet`, '__blank');
    }

    public async onReCAPTCHALoaded(data: any) {

        if (this.state.captchaLoaded === true) {
            this.props.onReCAPTCHAResult({ success: true });
            return;
        }

        if (this.reCAPTCHAIframe === undefined) {
            console.error('reset state here');
            return;
        }

        this.setState({ captchaLoaded: true });
    }

    public render() {

        return (

            <div className='Root'>

                <div className='Results-Container' >

                    <div className='Results-Settings-Container' >

                        <p className='Results-Label'> Results: {this.props.results.length} </p>
                        <span className='Span-Stretch'></span>

                        <div className='Button-Container' >

                            <div className='Button-Wrapper'>
                                <p className='Button-Label'>Settings</p>
                                <button style={ /* Temp Fix. Pulled 26px out of my ass hole. */ { width: '26px' }} className='Button' onClick={this.props.onSettingsClick.bind(null, false)}>
                                    <img className='Button-Image' src='./img/settings.svg' alt='clear' />
                                </button>
                            </div>

                            <div className='Button-Wrapper'>
                                <p className='Button-Label'>Clear</p>
                                <button style={ /* Temp Fix. Pulled 25px out of my ass hole. */ { width: '25px' }} className='Button' onClick={() => this.props.onClearClick(true)} >
                                    <img className='Button-Image' src='./img/trash.svg' alt='clear' />
                                </button>
                            </div>

                        </div>
                    </div>

                    {
                        this.props.isLoading === true ?

                            <div className='Loading-Container'>

                                <div className='Lds-Ring' >
                                    <div style={{ border: `8px solid ${this.props.settings.windowPrimaryColor}`, borderColor: `${this.props.settings.windowPrimaryColor} transparent transparent transparent` }} ></div>
                                    <div style={{ border: `8px solid ${this.props.settings.windowPrimaryColor}`, borderColor: `${this.props.settings.windowPrimaryColor} transparent transparent transparent` }}></div>
                                    <div style={{ border: `8px solid ${this.props.settings.windowPrimaryColor}`, borderColor: `${this.props.settings.windowPrimaryColor} transparent transparent transparent` }}></div>
                                    <div style={{ border: `8px solid ${this.props.settings.windowPrimaryColor}`, borderColor: `${this.props.settings.windowPrimaryColor} transparent transparent transparent` }}></div>
                                </div>

                            </div>

                            :

                            this.props.reCAPTCHAData !== undefined ?

                                <div className='ReCAPTCHA-Container' style={{ marginTop: '45px' }} >

                                    <iframe ref={(iframe: HTMLIFrameElement) => { this.reCAPTCHAIframe = iframe; }} onLoad={this.onReCAPTCHALoaded.bind(this)} title='ReCAPTCHA' src={this.props.reCAPTCHAData.url} scrolling='no' style={{ overflow: 'auto', minWidth: '100%', width: '332px', border: 'none', marginTop: '-68px', marginLeft: '-28px', marginBottom: '-6px' }}></iframe>

                                    <div className='Captcha'>
                                        <p>Having troubles?</p>
                                        <a href='#' onClick={this.onReCAPTCHAManualFix.bind(this)}>Click here to manually reset this captcha</a>
                                    </div>

                                </div>

                                :

                                <div className='Result-Container'>

                                    {this.props.results.map((result: QuizletSet, index: number) =>

                                        <div className='Result-Wrapper-Container'>

                                            {
                                                this.props.randomizedPromotion !== undefined && index === this.props.randomizedPromotion.index &&

                                                <div className='Result'>

                                                    <div className='Result-Title'>
                                                        <h1 className='Result-Header' style={{ fontWeight: 600, color: this.props.settings.windowSecondaryColor }}>{this.props.randomizedPromotion.promotion.header}</h1>
                                                        <button className='Result-Visit-Website-Button' style={{ fontWeight: 600, color: this.props.settings.windowSecondaryColor }} onClick={() => this.onVisitPromotionalWebsite(this.props.randomizedPromotion?.promotion as Promotion)}>{this.props.randomizedPromotion?.promotion?.goldPromotion ? 'Subscribe' : 'Visit Website'}</button>
                                                    </div>

                                                    <p className='Result-Answer' style={{ lineHeight: '1.20rem' }} >{this.props.randomizedPromotion.promotion.body}</p>

                                                    <span className='Span'></span>

                                                </div>
                                            }

                                            <div className='Result'>

                                                <div className='Result-Title'>
                                                    <h1 className='Result-Header' style={{ color: this.props.settings.windowSecondaryColor }}> Answer #{index + 1}</h1>
                                                    <button className='Result-Visit-Website-Button' style={{ color: this.props.settings.windowSecondaryColor }} onClick={() => this.onVisitWebsiteClick(result as QuizletSet, index + 1)}>Visit Website</button>
                                                </div>

                                                <p style={{ lineHeight: '1.20rem' }} className='Result-Answer'>{result.answer}</p>

                                                <span className='Span'></span>
                                                <p className='Result-Confidence'>Confidence: {Math.round(result.confidence * 100)}%</p>

                                            </div>

                                        </div>

                                    )}

                                    {this.props.noResults === true ?

                                        <div className='No-Results'>
                                            <p>Not what you're looking for?</p>
                                            {/* All in one line 'Try searching Google OR Report a problem */}

                                            <span>
                                                <a href='#' onClick={this.onTrySearchingWithGoogleClick.bind(this)}>Try searching Google</a>
                                                 {' or '}
                                                <a href='#' onClick={this.onReportAProblemClick.bind(this)}>report a problem</a>
                                            </span>
                                        </div>

                                        : null
                                    }

                                </div>
                    }
                </div>
            </div>
        );
    }

}
