import { BackgroundMessage, MessageType } from './base/AbstractRuntime';

import { Theme } from '../../types/theme';
import { State } from '../../types/state';
import { ActivateAccountResponse, LoginResponse, RequestError } from '../../types/authentication';

export interface QuizzardRuntimeEventListener {
    onLoadApplicationData(data: { themes: Theme[], isGold: boolean }): void;
    onStateUpdate(state: State): void;
}

type RuntimePort = chrome.runtime.Port | browser.runtime.Port;
const Runtime = process.env['MANIFEST_VERSION'] === '2' ? browser.runtime : chrome.runtime;

const safelyResolve = (promise: any, data: any): void => {
        
    if (promise === undefined) {
        return;
    }

    promise.resolve(data);
}

const constructBackgroundMessage = (data: any, messageType: MessageType): BackgroundMessage => {
    return { data, messageType };
}

export default class QuizzardRuntime {

    private static readonly DEFAULT_RUNTIME_ID = 'QuizzardComm';

    private runtimePort: RuntimePort | undefined;
    private loginPromise: any;
    private logoutPromise: any;
    private signUpPromise: any;
    private activationPromise: any;
    private forgotPasswordPromise: any;
    private freeTrialActivationPromise: any;

    private readonly quizzardRuntimeEventListener: QuizzardRuntimeEventListener;

    public constructor(quizzardRuntimeEventListener: QuizzardRuntimeEventListener) {
        this.quizzardRuntimeEventListener = quizzardRuntimeEventListener;
        this.runtimePort = QuizzardRuntime.initializeRuntime(this.onDisconnect.bind(this), this.onMessage.bind(this));
    }

    public disconnect(): void {

        if (this.runtimePort === undefined) {
            return;
        }

        this.runtimePort.disconnect();
    }

    public login(email: string, password: string, rememberMe: boolean): Promise<LoginResponse> {
       
        this.sendMessage(constructBackgroundMessage({ email, password, rememberMe }, MessageType.LOGIN));

        return new Promise<LoginResponse>((resolve) => {
            this.loginPromise = { resolve };
        });

    }

    public logout(): Promise<void> {

        this.sendMessage(constructBackgroundMessage({}, MessageType.LOGOUT));

        return new Promise<void>((resolve) => {
            this.logoutPromise = { resolve };
        });
    }

    public signup(email: string, password: string, rememberMe: boolean): Promise<RequestError | undefined> {
        
        this.sendMessage(constructBackgroundMessage({ email, password, rememberMe }, MessageType.SIGN_UP));

        return new Promise<RequestError | undefined>((resolve) => {
            this.signUpPromise = { resolve };
        });
    }

    public activate(email: string, activationCode: string): Promise<ActivateAccountResponse> {
        
        this.sendMessage(constructBackgroundMessage({ email, activationCode }, MessageType.ACTIVATE_ACCOUNT));

        return new Promise<ActivateAccountResponse>((resolve) => {
            this.activationPromise = { resolve };
        });
    }

    public activateFreeTrial(): Promise<{ goldSearchEndTimeEpoch?: number }> {

        this.sendMessage(constructBackgroundMessage({}, MessageType.ACTIVATE_FREE_TRIAL));

        return new Promise<{ goldSearchEndTimeEpoch?: number }>((resolve) => {
            this.freeTrialActivationPromise = { resolve };
        });
    }

    public forgotPassword(email: string): Promise<void> {
        
        this.sendMessage(constructBackgroundMessage({ email }, MessageType.FORGOT_PASSWORD));

        return new Promise<void>((resolve) => {
            this.forgotPasswordPromise = { resolve };
        });
    }

    public resetSignUpSession(): void {
        this.sendMessage(constructBackgroundMessage({}, MessageType.RESET_SIGN_UP_SESSION));
    }

    public agreeToTermsOfService(amountScrolled: number): void {
        this.sendMessage(constructBackgroundMessage({ amountScrolled }, MessageType.AGREE_TO_TERMS_OF_SERVICE));
    }

    public updateTheme(theme: Theme): void {
        this.sendMessage(constructBackgroundMessage(theme, MessageType.UPDATE_THEME));
    }

    public updateSearchSettings(confidenceThreshold: number, autoSort: boolean, autoClear: boolean): void {
        this.sendMessage(constructBackgroundMessage({ confidenceThreshold, autoSort, autoClear }, MessageType.UPDATE_SEARCH_SETTINGS));
    }

    public updateApplicationState(state: State): void {
        this.sendMessage(constructBackgroundMessage(state, MessageType.UPDATE_STATE));
    }

    public search(question: string): void {
        this.sendMessage(constructBackgroundMessage({ question }, MessageType.SEARCH_START));
    }

    public onSettingsClick(): void {
        this.sendMessage(constructBackgroundMessage({ }, MessageType.SETTINGS_MODAL_CLICK));
    }

    public onThemesClick(): void {
        this.sendMessage(constructBackgroundMessage({ }, MessageType.THEMES_MODAL_CLICK));
    }

    public onDonateClick(): void {
        this.sendMessage(constructBackgroundMessage({ }, MessageType.DONATE_CLICK));
    }

    public onDiscordClick(): void {
        this.sendMessage(constructBackgroundMessage({ }, MessageType.DISCORD_CLICK));
    }

    public onSubscribeClick(): void {
        this.sendMessage(constructBackgroundMessage({ }, MessageType.SUBSCRIBE_CLICK));
    }

    public onFreeTrialClick(): void {
        this.sendMessage(constructBackgroundMessage({ }, MessageType.FREE_TRIAL_CLICK));
    }

    public onWebsiteClick(index: number): void {
        this.sendMessage(constructBackgroundMessage({ index }, MessageType.WEBSITE_CLICK));
    }

    public onPromotionClick(id: number): void {
        this.sendMessage(constructBackgroundMessage({ id }, MessageType.PROMOTION_CLICK));
    }

    private sendMessage(backgroundMessage: BackgroundMessage): void {

        if (this.runtimePort === undefined) {
            this.runtimePort = QuizzardRuntime.initializeRuntime(this.onDisconnect.bind(this), this.onMessage.bind(this));
        }

        this.runtimePort.postMessage(backgroundMessage);
    }

    private onDisconnect(): void {

        if (this.runtimePort === undefined) {
            return;
        }

        this.runtimePort.onDisconnect.removeListener(this.onDisconnect.bind(this));
        this.runtimePort.onMessage.removeListener(this.onMessage.bind(this) as any);
        this.runtimePort = undefined;
    }
    
    private onMessage(message: BackgroundMessage): void {

        if (message.messageType === MessageType.LOAD_APPLICATION_DATA) {
            this.quizzardRuntimeEventListener.onLoadApplicationData(message.data as any);
        }

        else if (message.messageType === MessageType.UPDATE_STATE) {
            this.quizzardRuntimeEventListener.onStateUpdate(message.data as State);
        }

        else if (message.messageType === MessageType.LOGIN) {
            safelyResolve(this.loginPromise, message.data as LoginResponse);
        }

        else if (message.messageType === MessageType.LOGOUT) {
            safelyResolve(this.logoutPromise, undefined);
        }

        else if (message.messageType === MessageType.SIGN_UP) {
            safelyResolve(this.signUpPromise, message.data as RequestError | undefined);
        }

        else if (message.messageType === MessageType.ACTIVATE_ACCOUNT) {
            safelyResolve(this.activationPromise, message.data as ActivateAccountResponse);
        }

        else if (message.messageType === MessageType.ACTIVATE_FREE_TRIAL) {
            safelyResolve(this.freeTrialActivationPromise, message.data as any);
        }

        else if (message.messageType === MessageType.FORGOT_PASSWORD) {
            safelyResolve(this.forgotPasswordPromise, undefined);
        }

        else if (message.messageType === MessageType.ERROR_OCCURRED) {
            console.error(message.data);
        }

    }

    private static initializeRuntime(onDisconnect: () => void, onMessage: (backgroundMessage: BackgroundMessage) => void): RuntimePort {

        const runtimePort: RuntimePort = Runtime.connect({ name: QuizzardRuntime.DEFAULT_RUNTIME_ID });
        runtimePort.onDisconnect.addListener(onDisconnect);
        runtimePort.onMessage.addListener(onMessage as any);

        return runtimePort;
    }

}
