import './Settings.scss';

import { Component } from 'react';
import { Settings } from '../../types/settings';
import { User } from '../../types/user';

import { Time } from '../../util/Time';

type SettingsComponentProperties = {
    user: User | undefined;
    settings: Settings;
    display: boolean;
    displayGoldPromotion: boolean;
    onFreeTrial(): Promise<any>;
    onSubscribe(): void;
    onLoginOrSignUp(): void;
    onLogout(): void;
    onDiscordClick(): void;
    onDonateClick(): void;
    onClose: (confidenceThreshold: number, autoSort: boolean, autoClear: boolean) => void;
};

type SettingsComponentState = {
    displayFreeTrial: boolean;
    displayConfirmLogout: boolean;
    displayConfirmFreeTrial: boolean;
    isFreeTrialLoading: boolean;
    settings: Settings;
};

export default class SettingsComponent extends Component<SettingsComponentProperties, SettingsComponentState> {

    private static readonly DONATION_URL: string = 'https://www.quizzardapp.com/api/v2/extension/donation';
    private static readonly JOIN_DISCORD_URL: string = 'https://www.quizzardapp.com/join-discord';

    private static readonly MANAGE_QUIZZARD_GOLD_SUBSCRIPTION_URL: string = 'https://www.quizzardapp.com/manage-subscription';

    private inputElement: HTMLInputElement | undefined;

    constructor(props: any) {

        super(props);

        let displayFreeTrial: boolean = false;

        if (props.displayGoldPromotion === true) {
            displayFreeTrial = true;
        }

        this.state = { displayFreeTrial, displayConfirmLogout: false, displayConfirmFreeTrial: false, settings: this.props.settings, isFreeTrialLoading: false };
    }

    componentWillReceiveProps(newProps: any) {
        this.onInputChange();
        this.setState({ settings: newProps.settings, displayFreeTrial: newProps.displayGoldPromotion });
    }

    public onConfidenceThresholdChange(event: any): void {

        const settings: Settings = this.state.settings;
        settings.confidenceThreshold = parseInt(event.target.value, 10);

        this.setState({ settings });
    }

    public onAutomaticallySortChange(): void {

        const settings: Settings = this.state.settings;
        settings.autoSort = !settings.autoSort;

        this.setState({ settings });
    }

    public onAutomaticallyClearChange(): void {

        const settings: Settings = this.state.settings;
        settings.autoClear = !settings.autoClear;

        this.setState({ settings });
    }

    private onInputChange(): void {

        if (this.inputElement === undefined || this.inputElement === null) {
            return;
        }

        const value: number = parseInt((this.inputElement as any).value, 10) || 0;
        const min: number = parseInt((this.inputElement as any).min, 10);
        const max: number = parseInt((this.inputElement as any).max, 10);
        const ratioValue = (value - min) / (max - min) * 100;

        (this.inputElement as any).style.background = `linear-gradient(to right, #5A8FFA 0%, #5A8FFA ${ratioValue}%, #F2F2F2 ${ratioValue}%, #F2F2F2 100%)`;
    }

    public onSubscribeClick(): void {

        if (this.props.user === undefined) {
            this.setState({ displayFreeTrial: false, displayConfirmFreeTrial: false });
            this.props.onLoginOrSignUp();
            return;
        }

        this.props.onSubscribe();
    }

    public onLoginOrSignUpClick(): void {
        this.props.onLoginOrSignUp();
    }

    public onConfirmLogoutClick(): void {
        this.setState({ displayConfirmLogout: true });
    }

    public onLogoutClick(): void {
        this.setState({ displayConfirmLogout: false });
        this.props.onLogout();
    }

    private onBackToSettingsClick(): void {

        /*
         * 2023-03-21 - Workaround to update the slider if the user
         * clicks the back button.
         */

        setTimeout(() => this.onInputChange(), 25);
        this.setState({ displayFreeTrial: false, displayConfirmLogout: false, displayConfirmFreeTrial: false, isFreeTrialLoading: false });
    }

    private onCloseClick(): void {
        this.setState({ displayFreeTrial: false, displayConfirmLogout: false, displayConfirmFreeTrial: false, isFreeTrialLoading: false });
        this.props.onClose(this.state.settings.confidenceThreshold, this.state.settings.autoSort, this.state.settings.autoClear);
    }

    private onDiscordClick(): void {
        this.props.onDiscordClick();
        window.open(SettingsComponent.JOIN_DISCORD_URL, '_blank');
    }

    private onDonateClick(): void {
        this.props.onDonateClick();
        window.open(SettingsComponent.DONATION_URL, '_blank');
    }

    public render() {

        return (

            <div style={{ display: this.props.display === true ? 'flex' : 'none' }} className='popup-container Fade-In'>

                <style>
                    {`
                          .input-slider-confidence::after {
                            content: '${this.state.settings.confidenceThreshold}%';
                            color: #576574;
                            text-align: right;
                            position: absolute;
                            right: 100px;
                            top: 70px;
                            opacity: 0.4;
                          }

                    `}


                </style>

                {
                    this.state.displayFreeTrial === true && this.state.displayConfirmLogout === false ?

                        <div id='Settings' className='Settings-Container' >

                            <button className='Popup-Back-Button' onClick={this.onBackToSettingsClick.bind(this)}><img src='img/back.svg' alt='Back' /></button>
                            <button className='Settings-Close-Button' onClick={this.onCloseClick.bind(this)}><img src='img/close.svg' alt='Close' /></button>

                            <div className='Free-Trial-Container'>

                                <div className='Free-Trial-Body'>
                                    <img src='img/quizzard-gold.png' alt='quizzard-gold' />
                                    <div className='Free-Trial-Body-Content'>
                                        <h1>Quizzard Gold</h1>
                                        <p>Double your search speed, accuracy, and bypass 'I am not a robot' reCAPTCHA!</p>
                                    </div>
                                </div>
                                <div className="Free-Trial-Button-Container">
                                    <button tabIndex={5} className='Subscribe-Button' onClick={this.onSubscribeClick.bind(this)}>Subscribe</button>
                                </div>
                            </div>

                        </div>

                        : this.state.displayFreeTrial === false && this.state.displayConfirmLogout === true ?
                            <div id='Settings' className='Settings-Container' >

                                <button className='Popup-Back-Button' onClick={this.onBackToSettingsClick.bind(this)}><img src='img/back.svg' alt='Back' /></button>
                                <button className='Settings-Close-Button' onClick={this.onCloseClick.bind(this)}><img src='img/close.svg' alt='Close' /></button>

                                <div className='Logout-Container'>
                                    <h1>Are you sure you want to logout?</h1>
                                    {
                                        this.props.user !== undefined &&
                                        <div className='User-Data-Container'>
                                            <h1>User ID: {this.props.user.userID}</h1>
                                            <h1>Email:  {this.props.user.email}</h1>
                                        </div>
                                    }
                                    <button tabIndex={4} className='Logout-Button' onClick={this.onLogoutClick.bind(this)}>Logout</button>
                                </div>
                            </div>
                            :
                            <div id='Settings' className='Settings-Container' >

                                <button className='Settings-Close-Button' onClick={this.props.onClose.bind(null, this.state.settings.confidenceThreshold, this.state.settings.autoSort, this.state.settings.autoClear)}><img src='img/close.svg' alt='Close' /></button>

                                <div className='slider-container'>
                                    <h1>Confidence Threshold</h1>
                                    <input ref={(input: HTMLInputElement) => { this.inputElement = input; }} className='input-slider-confidence input-hover' type='range' min='1' max='100' value={this.state.settings.confidenceThreshold} onInput={this.onInputChange.bind(this)} onChange={this.onConfidenceThresholdChange.bind(this)} />
                                </div>

                                <div className='toggle-container'>

                                    <div className='item'>
                                        <p>Automatically Sort</p>
                                        <span></span>
                                        <label className='switch'>
                                            <input className='input-hover' type='checkbox' value={this.state.settings.autoSort as any} onChange={this.onAutomaticallySortChange.bind(this)} checked={this.state.settings.autoSort} />
                                            <div className='Slider'></div>
                                        </label>
                                    </div>

                                    <div className='item'>
                                        <p>Automatically Clear</p>
                                        <span></span>
                                        <label className='switch'>
                                            <input className='input-hover' type='checkbox' value={this.state.settings.autoClear as any} onChange={this.onAutomaticallyClearChange.bind(this)} checked={this.state.settings.autoClear} />
                                            <div className='Slider'></div>
                                        </label>
                                    </div>

                                    <div className='item'>
                                        <p>Quizzard Gold</p>
                                        <span></span>
                                        <div className='Quizzard-Gold-Container'>
                                            {
                                                this.props.user?.isGoldSearch === true && this.props.user?.goldSearchEndTimeEpoch !== undefined ?
                                                    <>
                                                        {
                                                            this.props.user.isRecurringSubscription === true ?
                                                                <>
                                                                    <div className='Renew-Subscription-Container'>
                                                                        <p className='Quizzard-Gold-Expiration-Label'>Renews {Time.getFormattedRelativeDate(this.props.user.goldSearchEndTimeEpoch * 1000)}</p>
                                                                        <button className='Manage-Subscription-Button' onClick={SettingsComponent.onManageSubscriptionClick}>Manage Subscription</button>
                                                                    </div>
                                                                </>
                                                                :
                                                                <p className='Quizzard-Gold-Expiration-Label'>Expires {Time.getFormattedRelativeDate(this.props.user.goldSearchEndTimeEpoch * 1000)}</p>
                                                        }
                                                    </>
                                                    :
                                                    <>
                                                        <button className='Subscribe-Button' onClick={this.onSubscribeClick.bind(this)}>Subscribe</button>
                                                        {/* <button disabled={this.props.user !== undefined && this.props.user.isFreeTrialActivated === true} className='Trial-Button' onClick={this.onFreeTrialClick.bind(this)}>Free Trial</button> */}
                                                    </>
                                            }
                                        </div>

                                    </div>

                                </div>

                                <div className='button-container'>

                                    <button onClick={this.onDonateClick.bind(this)}>
                                        <img className='button-icon' src='img/donate-icon.png' alt='Donate' />
                                        DONATE
                                    </button>

                                    <button onClick={this.onDiscordClick.bind(this)} >
                                        <img className='button-icon' src='img/discord.png' alt='Discord' />
                                        DISCORD
                                    </button>

                                    {
                                        this.props.user === undefined ?

                                            <button onClick={this.onLoginOrSignUpClick.bind(this)} >
                                                <img className='button-icon' src='img/login.svg' alt='Login/Sign-Up' />
                                                LOGIN/SIGN-UP
                                            </button>
                                            :
                                            <button onClick={this.onConfirmLogoutClick.bind(this)} >
                                                <img className='button-icon' src='img/logout.svg' alt='Logout' />
                                                LOGOUT
                                            </button>
                                    }

                                </div>

                            </div>

                }
            </div>

        );
    }

    private static onManageSubscriptionClick(): void {
        window.open(SettingsComponent.MANAGE_QUIZZARD_GOLD_SUBSCRIPTION_URL, '_blank');
    }

}
