import moment from 'moment';

const getFormattedRelativeDate = (dateTime: number): string => {

    const date = new Date(dateTime);
    const relativeTime: string = moment(dateTime).fromNow();

    // If time is in future don't display.
    if (dateTime - date.getTime() < 0) {
        return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    }

    return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} (${relativeTime})`;
};

const getCurrentTimeEpoch = (): number => {
    return Math.floor(new Date().getTime() / 1000);
};

export const Time = { getFormattedRelativeDate, getCurrentTimeEpoch };