import './Theme.scss';

import { Component } from 'react';
import { Theme as ThemeType } from '../../../types/theme';

export default class Theme extends Component<{ theme: ThemeType, selectedColor: string, onThemeClick: (theme: ThemeType) => void }> {

    private renderedComponent: any;

    public render() {

        const isSelected: boolean = this.props.theme.primaryColor === this.props.selectedColor;

        this.renderedComponent = (<button style={{ backgroundColor: this.props.theme.primaryColor, border: `4px solid ${this.props.theme.secondaryColor}`, opacity: 1.0 }} className='Theme' onClick={this.props.onThemeClick.bind(this, this.props.theme)} > </button>);

        if (isSelected === true) {
            this.renderedComponent = (<button disabled={true} style={{ backgroundColor: this.props.theme.primaryColor, border: `4px solid ${this.props.theme.secondaryColor}`, opacity: 0.10 }} className='Theme' onClick={this.props.onThemeClick.bind(this, this.props.theme)} ><img style={{ width: '65%' }} src='./img/check.svg' alt='Check'></img> </button>);
        }

        return this.renderedComponent;
    }

}