import './Auth.scss';
import { Component } from 'react';
import { RequestError, ActivateAccountResponse, LoginResponse } from '../../types/authentication';
import { createPopup } from '../../util/Window';
import { User } from '../../types/user';

import BrowserStorage from '../../browser/BrowserStorage';

enum AuthType {
    NONE,
    LOGIN,
    ACTIVATE_USER,
    SIGN_UP,
    FORGOT_PASSWORD,
    FORGOT_PASSWORD_CONFIRMED,
}

type AuthComponentProperties = {
    display: boolean;
    onResetSignUpSession: () => void;
    onPopupClosed: (userData?: User) => void;
    onForgotPassword: (email: string) => Promise<void>
    onSignUp: (email: string, password: string, rememberMe: boolean) => Promise<RequestError | undefined>;
    onLogin: (email: string, password: string, rememberMe: boolean) => Promise<LoginResponse>;
    onActivate: (email: string, activationCode: string) => Promise<ActivateAccountResponse>;
};

type AuthComponentState = {
    authType: AuthType;
    email: string;
    password: string;
    activationCode: string;
    rememberMe: boolean;
    error: RequestError | undefined;
    isLoading: boolean;
};

const focusIfNotNull = (element: HTMLElement | null): void => {

    if (element == null) {
        return;
    }

    element.focus();
};

const focusAfterMS = (element: HTMLElement | null, ms: number = 0): void => {
    setTimeout(() => focusIfNotNull(element), ms);
};

const WEIRD_RESPONSE_ERROR: number = 679;

const AUTH_TYPE_MAP: Record<AuthType, string> = {
    [AuthType.NONE]: '',
    [AuthType.LOGIN]: 'Login',
    [AuthType.ACTIVATE_USER]: 'Activate Account',
    [AuthType.SIGN_UP]: 'Sign Up',
    [AuthType.FORGOT_PASSWORD]: 'Forgot Password',
    [AuthType.FORGOT_PASSWORD_CONFIRMED]: 'Forgot Password'
};

export default class Auth extends Component<AuthComponentProperties, AuthComponentState> {

    public emailInputElement: HTMLInputElement | null;
    public passwordInputElement: HTMLInputElement | null;
    public errorButtonElement: HTMLButtonElement | null;

    public constructor(props: AuthComponentProperties) {
        super(props);

        this.emailInputElement = null;
        this.passwordInputElement = null;
        this.errorButtonElement = null;

        BrowserStorage.get(['isActivationRequired', 'email', 'rememberMe'], ({ isActivationRequired, email, rememberMe }) => {

            let authType: AuthType = AuthType.SIGN_UP;

            if (isActivationRequired === true) {
                authType = AuthType.ACTIVATE_USER;
            }

            if (rememberMe === true) {
                this.setState({ authType, email, rememberMe: true });
                return;
            }

            this.setState({ authType, email: '', rememberMe: false });
        });

        this.state = { authType: AuthType.SIGN_UP, email: '', password: '', activationCode: '', rememberMe: false, error: undefined, isLoading: false };
    }

    public onPopupClosed(): void {
        this.setState({ password: '', activationCode: '' });
        this.props.onPopupClosed();
    }

    public onLoginClick(): void {

        // this.setState({ isLoading: true });
        // setTimeout(() => this.setState({ isLoading: false }), 5000);
        // return;

        const email: string = this.state.email.trim();
        const password = this.state.password.trim();
        const rememberMe: boolean = this.state.rememberMe;

        if (email.length === 0 && password.length === 0) {
            return;
        }

        if (email.length === 0 && password.length !== 0) {
            focusIfNotNull(this.emailInputElement);
            return;
        }

        if (password.length === 0 && email.length !== 0) {
            focusIfNotNull(this.passwordInputElement);
            return;
        }

        this.setState({ isLoading: true });
        this.props.onLogin(email, password, rememberMe).then((loginResponse: LoginResponse) => {

            if (loginResponse === undefined) {
                return;
            }

            if (loginResponse.error !== undefined) {
                this.setState({ error: loginResponse.error });
                focusAfterMS(this.errorButtonElement, 500);
                return;
            }

            if (loginResponse.user === undefined) {
                this.setState({ error: { code: WEIRD_RESPONSE_ERROR, message: 'An unknown error occured during login! Please try-again.' } });
                return;
            }

            this.setState({ password: '', activationCode: '' });
            this.props.onPopupClosed({ email, userID: loginResponse.user.userID, isGoldSearch: loginResponse.user.isGoldSearch, isFreeTrialActivated: loginResponse.user.isFreeTrialActivated, goldSearchEndTimeEpoch: loginResponse.user.goldSearchEndTimeEpoch });

        }).finally(() => {
            this.setState({ isLoading: false });
        });

    }

    public onSignUpClick(): void {

        const email: string = this.state.email.trim();
        const password = this.state.password.trim();
        const rememberMe: boolean = this.state.rememberMe;

        if (email.length === 0 && password.length === 0) {
            return;
        }

        if (email.length === 0 && password.length !== 0) {
            focusIfNotNull(this.emailInputElement);
            return;
        }

        if (password.length === 0 && email.length !== 0) {
            focusIfNotNull(this.passwordInputElement);
            return;
        }

        this.setState({ isLoading: true });
        this.props.onSignUp(email, password, rememberMe).then((error: RequestError | undefined) => {

            if (error !== undefined) {
                this.setState({ error });
                focusAfterMS(this.errorButtonElement, 500);
                return;
            }

            this.setState({ authType: AuthType.ACTIVATE_USER });

        }).finally(() => {
            this.setState({ isLoading: false });
        });
    }

    public onActivateClick(): void {

        const activationCode: string = this.state.activationCode.trim();
        const email: string = this.state.email.trim();

        if (activationCode.length === 0) {
            return;
        }

        this.setState({ isLoading: true });
        this.props.onActivate(email, activationCode).then((activateAccountResponse: ActivateAccountResponse) => {

            if (activateAccountResponse.error !== undefined) {
                this.setState({ error: activateAccountResponse.error });
                return;
            }

            if (activateAccountResponse.userID === undefined) {
                this.setState({ error: { code: WEIRD_RESPONSE_ERROR, message: 'An unknown error occured during activation! Please try-again.' } });
                return;
            }

            this.setState({ password: '', activationCode: '' });
            this.props.onPopupClosed({ email, userID: activateAccountResponse.userID, isGoldSearch: false, isFreeTrialActivated: false, goldSearchEndTimeEpoch: undefined });

        }).finally(() => {
            this.setState({ isLoading: false });
        });
    }

    public OnForgotPasswordConfirmedGoBackClick(): void {
        this.setState({ authType: AuthType.LOGIN });
    }

    public onForgotPasswordClick(): void {

        const email: string = this.state.email.trim();

        if (email.length === 0) {
            focusIfNotNull(this.emailInputElement);
            return;
        }

        this.setState({ isLoading: true });
        this.props.onForgotPassword(email).finally(() => this.setState({ isLoading: false, authType: AuthType.FORGOT_PASSWORD_CONFIRMED }));
    }

    public onNotNowClick(): void {
        this.props.onPopupClosed();
    }

    public onForgotPasswordViewClick(): void {
        this.setState({ authType: AuthType.FORGOT_PASSWORD });
    }

    public onViewSignUpClick(): void {
        this.setState({ authType: AuthType.SIGN_UP });
    }

    public onViewLoginClick(): void {
        this.setState({ authType: AuthType.LOGIN });
    }

    public onErrorGoBackClick(): void {
        this.setState({ error: undefined });
    }

    public onBackToSignUp(): void {
        this.props.onResetSignUpSession();
        this.setState({ authType: AuthType.SIGN_UP });
    }

    public onEmailInputChanged(event: React.ChangeEvent<HTMLInputElement>): void {
        this.setState({ email: event.target.value });
    }

    public onPasswordInputChanged(event: React.ChangeEvent<HTMLInputElement>): void {
        this.setState({ password: event.target.value });
    }

    public onActivationInputChanged(event: React.ChangeEvent<HTMLInputElement>): void {
        this.setState({ activationCode: event.target.value });
    }

    public onRememberMeChanged(event: React.ChangeEvent<HTMLInputElement>): void {
        this.setState({ rememberMe: event.target.checked });
    }

    public onEnterKeyPressed(event: React.KeyboardEvent<HTMLInputElement>): void {

        if (event.key !== 'Enter') {
            return;
        }

        switch (this.state.authType) {

            case AuthType.LOGIN: {
                this.onLoginClick();
                break;
            }

            case AuthType.SIGN_UP: {
                this.onSignUpClick();
                break;
            }

            case AuthType.ACTIVATE_USER: {
                this.onActivateClick();
                break;
            }
        }
    }

    public render() {

        return (
            <div style={{ display: this.props.display === true ? 'flex' : 'none' }} className='Popup-Container Fade-In'>

                <div className='Auth-Wrapper-Container'>

                    {
                        this.state.isLoading === false ?
                            <div className='Auth-Container'>

                                <div className='Header-Input-Container'>

                                    {
                                        this.state.error !== undefined &&
                                        <>
                                            <span className='Error-Code-Label'>E{this.state.error.code.toString().padStart(4, '0')}</span>
                                            <span className='Flex-Region'></span>
                                        </>
                                    }

                                    {
                                        this.state.error === undefined && this.state.authType === AuthType.ACTIVATE_USER &&
                                        <button className='Popup-Back-Button' onClick={this.onBackToSignUp.bind(this)}><img src='img/back.svg' alt='Back' /></button>
                                    }

                                    <button onClick={this.onPopupClosed.bind(this)}>
                                        <img src='./img/close.svg' alt='Close'></img>
                                    </button>
                                </div>

                                {
                                    this.state.error === undefined ?
                                        <>
                                            <div className='Auth-Logo-Container'>
                                                <h1>{AUTH_TYPE_MAP[this.state.authType]}</h1>
                                            </div>

                                            <div className='Auth-Sign-In-Container'>

                                                {
                                                    this.state.authType === AuthType.ACTIVATE_USER ?
                                                        <p className='Auth-Activate-Message'>Please enter the verification code sent to your email. If you don't see the email, please check your spam folder. If you did not receive an email, please go back and try again.</p>
                                                    :
                                                    this.state.authType === AuthType.FORGOT_PASSWORD ?
                                                        <p className='Auth-Forgot-Password-Message'>Enter the email associated with your Quizzard account and we'll send you instructions on how to reset your password.</p>
                                                    :
                                                    this.state.authType === AuthType.FORGOT_PASSWORD_CONFIRMED ?
                                                    <p className='Auth-Forgot-Password-Message'>An email with instructions on how to reset your password was just sent to your inbox.</p>
                                                    :
                                                    <></>
                                                }

                                                <div className='Auth-Sign-In-Input-Container'>
                                                    {
                                                        (this.state.authType === AuthType.LOGIN || this.state.authType === AuthType.SIGN_UP) ?
                                                            <>
                                                                <input
                                                                    ref={(input) => this.emailInputElement = input}
                                                                    tabIndex={1} value={this.state.email}
                                                                    onChange={this.onEmailInputChanged.bind(this)}
                                                                    onKeyDown={this.onEnterKeyPressed.bind(this)}
                                                                    className='Auth-Sign-In-Input'
                                                                    type='text'
                                                                    placeholder='Email'
                                                                />

                                                                <input
                                                                    ref={(input) => this.passwordInputElement = input}
                                                                    tabIndex={2}
                                                                    value={this.state.password}
                                                                    onChange={this.onPasswordInputChanged.bind(this)}
                                                                    onKeyDown={this.onEnterKeyPressed.bind(this)}
                                                                    className='Auth-Sign-In-Input'
                                                                    type='password'
                                                                    placeholder='Password'
                                                                />

                                                                <div className='Auth-Sign-In-Options-Container'>

                                                                    <div className='Auth-Sign-In-Remember-Me-Container'>
                                                                        <input
                                                                            tabIndex={3}
                                                                            checked={this.state.rememberMe}
                                                                            onChange={this.onRememberMeChanged.bind(this)}
                                                                            type='checkbox'
                                                                            id='remember-me'
                                                                            name='remember-me'
                                                                        />
                                                                        <label htmlFor='remember-me'>Remember me</label>
                                                                    </div>
                                                                    {
                                                                        this.state.authType === AuthType.LOGIN &&
                                                                        <a tabIndex={4} onClick={this.onForgotPasswordViewClick.bind(this)}>Forgot password?</a>
                                                                    }
                                                                </div>

                                                            </>
                                                            : this.state.authType === AuthType.FORGOT_PASSWORD ?

                                                                <input
                                                                    ref={(input) => this.emailInputElement = input}
                                                                    tabIndex={1}
                                                                    value={this.state.email}
                                                                    onChange={this.onEmailInputChanged.bind(this)}
                                                                    onKeyDown={this.onEnterKeyPressed.bind(this)}
                                                                    className='Auth-Sign-In-Input'
                                                                    type='text'
                                                                    placeholder='Email'
                                                                />

                                                                : this.state.authType === AuthType.ACTIVATE_USER ?

                                                                    <input
                                                                        tabIndex={1}
                                                                        value={this.state.activationCode}
                                                                        onChange={this.onActivationInputChanged.bind(this)}
                                                                        onKeyDown={this.onEnterKeyPressed.bind(this)}
                                                                        className='Auth-Sign-In-Input'
                                                                        type='text'
                                                                        placeholder='Activation Code'
                                                                    />
                                                                    : this.state.authType === AuthType.FORGOT_PASSWORD_CONFIRMED ?
                                                                    <img src='./img/success.png' alt='Success' className='Forgot-Password-Success-Icon'></img>
                                                                    :
                                                                    <></>
                                                    }
                                                    <div className='Auth-Sign-In-Button-Container'>
                                                        {
                                                            this.state.authType === AuthType.LOGIN ?
                                                                <button tabIndex={5} className='Auth-Sign-In-Button' onClick={this.onLoginClick.bind(this)}>Login</button>
                                                                :
                                                                this.state.authType === AuthType.SIGN_UP ?
                                                                    <>
                                                                        <button tabIndex={4} className='Auth-Sign-In-Button' onClick={this.onSignUpClick.bind(this)}>Sign Up</button>
                                                                        <button tabIndex={5} className='Auth-Sign-In-Later-Button' onClick={this.onNotNowClick.bind(this)}>Not Now</button>

                                                                    </>
                                                                    :
                                                                    this.state.authType === AuthType.FORGOT_PASSWORD ?
                                                                        <button tabIndex={2} className='Auth-Sign-In-Button' onClick={this.onForgotPasswordClick.bind(this)}>Submit</button>
                                                                        :
                                                                        this.state.authType === AuthType.ACTIVATE_USER ?
                                                                            <button tabIndex={2} className='Auth-Sign-In-Button' onClick={this.onActivateClick.bind(this)}>Activate</button>
                                                                            :
                                                                            this.state.authType === AuthType.FORGOT_PASSWORD_CONFIRMED ?
                                                                            <button tabIndex={2} className='Auth-Sign-In-Button' onClick={this.OnForgotPasswordConfirmedGoBackClick.bind(this)}>Go Back</button>
                                                                            :
                                                                            <></>
                                                        }
                                                    </div>



                                                    {
                                                        (this.state.authType === AuthType.LOGIN || this.state.authType === AuthType.SIGN_UP) &&
                                                            <>
                                                                <div style={{ width: '65%', borderBottom: '1px solid #ccc', textAlign: 'center' }}>
                                                                    <span style={{ fontSize: '0.75rem', backgroundColor: 'white', padding: '0 10px', position: 'relative', top: '8px' }}>
                                                                        OR
                                                                    </span>
                                                                </div>

                                                                <div className='Auth-Alternative-Sign-In-Container'>
                                                                    <button className='Auth-Alternative-Sign-In-Button' onClick={Auth.onGoogleSignInClick}>
                                                                        <img src='./img/google-sign-in.png' alt='Google' />
                                                                        Google
                                                                    </button>
                                                                    <button className='Auth-Alternative-Sign-In-Button' onClick={Auth.onAppleSignInClick}>
                                                                        <img src='./img/apple-sign-in.png' alt='Apple' />
                                                                        Apple
                                                                    </button>
                                                                </div>
                                                            </>
                                                    }


                                                    {
                                                        (this.state.authType === AuthType.LOGIN) &&
                                                        <p>Don't have an account? <a tabIndex={6} onClick={this.onViewSignUpClick.bind(this)}>Sign up</a></p>
                                                    }
                                                    {
                                                        (this.state.authType === AuthType.SIGN_UP || this.state.authType === AuthType.FORGOT_PASSWORD) &&
                                                        <p>Already have an account? <a tabIndex={6} onClick={this.onViewLoginClick.bind(this)}>Login</a></p>
                                                    }

                                                </div>
                                            </div>
                                        </>
                                        :
                                        <>

                                            <div className='Auth-Error-Container'>

                                                <h1>{this.state.error.message}</h1>

                                                <div>
                                                    <img src='./img/error.svg' alt='Error'></img>
                                                </div>

                                                <div className='Auth-Sign-In-Input-Container'>
                                                    {
                                                        <div className='Auth-Error-Button-Container'>
                                                            <button id='btnError' ref={(btnError) => { this.errorButtonElement = btnError; }} className='Auth-Error-Button' onClick={this.onErrorGoBackClick.bind(this)}>Go Back</button>
                                                        </div>
                                                    }
                                                </div>
                                            </div>

                                        </>
                                }
                            </div>
                            :
                            <div style={{ height: '235px' }} className='Auth-Container'>
                                <div className='Auth-Loading-Container'>
                                    <div style={{ width: '100px', height: '100px' }} className='Lds-Ring'>
                                        <div style={{ width: '84px', height: '84px', border: `8px solid #54a0fc`, borderColor: `#54a0fc transparent transparent transparent` }} ></div>
                                        <div style={{ width: '84px', height: '84px', border: `8px solid #54a0fc`, borderColor: `#54a0fc transparent transparent transparent` }}></div>
                                        <div style={{ width: '84px', height: '84px', border: `8px solid #54a0fc`, borderColor: `#54a0fc transparent transparent transparent` }}></div>
                                        <div style={{ width: '84px', height: '84px', border: `8px solid #54a0fc`, borderColor: `#54a0fc transparent transparent transparent` }}></div>
                                    </div>

                                    <h1>Loading, please wait</h1>

                                </div>
                            </div>
                    }

                </div>
            </div>

        );
    }

    private static onGoogleSignInClick(): void {
        createPopup('https://www.quizzardapp.com/api/v2/extension/google-sign-in', 450, 600);
     }

    private static onAppleSignInClick(): void {
        createPopup('https://www.quizzardapp.com/api/v2/extension/apple-sign-in', 550, 750);
    }

}
