import './TermsAndConditions.scss';
import { Component } from 'react';

export default class TermsAndConditions extends Component<{ display: boolean, onIAgreeClick: (amountScrolled: number) => void }, { amountScrolled: number }> {

    private tosInfoContainer: HTMLDivElement | null;

    public constructor(props: any) {
        super(props);
        this.tosInfoContainer = null;
        this.state = { amountScrolled: 0 };
    }

    componentDidMount() {

        if (this.tosInfoContainer) {
            this.tosInfoContainer.addEventListener('scroll', this.handleScroll);
        }
    }

    componentWillUnmount() {
        if (this.tosInfoContainer) {
            this.tosInfoContainer.removeEventListener('scroll', this.handleScroll);
        }
    }

    handleScroll = () => {
        if (this.tosInfoContainer) {
            const { scrollTop, scrollHeight, clientHeight } = this.tosInfoContainer;
            const scrolled: number = (scrollTop / (scrollHeight - clientHeight)) * 100;
            const amountScrolled: number = Math.round(scrolled * 100) / 100;
            this.setState({ amountScrolled });
        }
    }

    public render() {

        return (
            <div style={{ display: this.props.display === true ? 'flex' : 'none' }} className='Popup-Container Fade-In'>

                <div className='Tos-Wrapper-Container' >
                    <div className='Tos-Container' >
                        <h1>Terms and Conditions</h1>
                        <div ref={(div) => { this.tosInfoContainer = div; }} onScroll={this.handleScroll} className='Tos-Info-Container'>

                            <h2>General Use</h2>
                            <p>
                                Quizzard is a Google Chrome extension search tool that is solely intended to assist users in rapidly finding Quizlet-hosted question sets.
                                It is strictly prohibited to use this tool to facilitate any form of academic misconduct on assessed material as follows, but not limited to:
                                quizzes, tests, exams, and alike.
                            </p>

                            <h2>DISCLAIMER</h2>
                            <p>
                                THE APPLICATION IS PROVIDED ON AN AS-IS AND AS-AVAILABLE BASIS. YOU AGREE THAT YOUR USE OF THE APPLICATION SERVICES
                                WILL BE AT YOUR SOLE RISK. TO THE FULLEST EXTENT PERMITTED BY LAW, WE DISCLAIM ALL WARRANTIES, EXPRESS OR IMPLIED,
                                IN CONNECTION WITH THE APPLICATION AND YOUR USE THEREOF, INCLUDING, WITHOUT LIMITATION, THE IMPLIED WARRANTIES OF
                                MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE, AND NON-INFRINGEMENT. WE MAKE NO WARRANTIES OR REPRESENTATIONS
                                ABOUT THE ACCURACY OR COMPLETENESS OF THE APPLICATIONS CONTENT OR THE CONTENT OF ANY WEBSITES LINKED TO THIS
                                APPLICATION AND WE WILL ASSUME NO LIABILITY OR RESPONSIBILITY FOR ANY (1) ERRORS, MISTAKES, OR INACCURACIES OF CONTENT
                                AND MATERIALS, (2) PERSONAL INJURY OR PROPERTY DAMAGE, OF ANY NATURE WHATSOEVER, RESULTING FROM YOUR ACCESS TO
                                AND USE OF THE APPLICATION, (3) ANY UNAUTHORIZED ACCESS TO OR USE OF OUR SECURE SERVERS AND/OR ANY AND ALL PERSONAL
                                INFORMATION AND/OR FINANCIAL INFORMATION STORED THEREIN, (4) ANY INTERRUPTION OR CESSATION OF TRANSMISSION TO
                                OR FROM THE APPLICATION, (5) ANY BUGS, VIRUSES, TROJAN HORSES, OR THE LIKE WHICH MAY BE TRANSMITTED TO OR
                                THROUGH THE APPLICATION BY ANY THIRD PARTY, AND/OR (6) ANY ERRORS OR OMISSIONS IN ANY CONTENT AND MATERIALS
                                OR FOR ANY LOSS OR DAMAGE OF ANY KIND INCURRED AS A RESULT OF THE USE OF ANY CONTENT POSTED, TRANSMITTED, OR
                                OTHERWISE MADE AVAILABLE VIA THE APPLICATION. WE DO NOT WARRANT, ENDORSE, GUARANTEE, OR ASSUME RESPONSIBILITY
                                FOR ANY PRODUCT OR SERVICE ADVERTISED OR OFFERED BY A THIRD PARTY THROUGH THE APPLICATION, ANY HYPERLINKED
                                WEBSITE, OR ANY WEBSITE OR MOBILE APPLICATION FEATURED IN ANY BANNER OR OTHER ADVERTISING, AND WE WILL NOT BE
                                A PARTY TO OR IN ANY WAY BE RESPONSIBLE FOR MONITORING ANY TRANSACTION BETWEEN YOU AND ANY THIRD-PARTY PROVIDERS
                                OF PRODUCTS OR SERVICES. AS WITH THE PURCHASE OF A PRODUCT OR SERVICE THROUGH ANY MEDIUM OR IN ANY ENVIRONMENT,
                                YOU SHOULD USE YOUR BEST JUDGMENT AND EXERCISE CAUTION WHERE APPROPRIATE.
                            </p>

                            <h2>LIMITATIONS OF LIABILITY</h2>
                            <p>
                                IN NO EVENT WILL WE OR OUR DIRECTORS, EMPLOYEES, OR AGENTS BE LIABLE TO YOU OR ANY THIRD PARTY FOR ANY DIRECT,
                                INDIRECT, CONSEQUENTIAL, EXEMPLARY, INCIDENTAL, SPECIAL, OR PUNITIVE DAMAGES, INCLUDING LOST PROFIT, LOST REVENUE,
                                LOSS OF DATA, OR OTHER DAMAGES ARISING FROM YOUR USE OF THE APPLICATION, EVEN IF WE HAVE BEEN ADVISED
                                OF THE POSSIBILITY OF SUCH DAMAGES.
                            </p>

                        </div>

                        <button className='Tos-Button' onClick={this.props.onIAgreeClick.bind(null, this.state.amountScrolled)} >I Agree</button>

                    </div>
                </div>
            </div>

        );
    }

}
