import './Themes.scss';
import Theme from './theme/Theme';

import { Component } from 'react';
import { Theme as ThemeType } from '../../types/theme';

export default class Themes extends Component<{ display: boolean, themes: Array<ThemeType>, selectedColor: string, onThemeChange: (theme: ThemeType) => void, onCloseClick: () => void }> {

    private onThemeClick(theme: ThemeType): void {
        this.props.onThemeChange(theme);
    }

    public render() {

        return (
            <div style={{ display: this.props.display === true ? 'flex' : 'none' }} className='popup-container Fade-In'>

                <button className='Pallete-Close-Button' onClick={this.props.onCloseClick}><img src='img/close.svg' alt='Close' /></button>

                <div id='Themes' className='Theme-Container' >

                    <h1>Select a theme</h1>
                    <div className='Themes'>

                        {
                            this.props.themes.map(theme =>
                                <Theme theme={theme} onThemeClick={this.onThemeClick.bind(this)} selectedColor={this.props.selectedColor} />
                            )
                        }

                    </div>
                </div>
            </div>

        );
    }

}
