import React from 'react';
import './App.scss';

import Themes from './components/themes/Themes';
import Results from './components/results/Results';
import SettingsComponent from './components/settings/Settings';
import TermsAndConditions from './components/tos/TermsAndConditions';
import Auth from './components/auth/Auth';
import BrowserStorage from './browser/BrowserStorage';
import QuizzardRuntime, { QuizzardRuntimeEventListener } from './browser/runtime/QuizzardRuntime';
import { Time } from './util/Time';

import { Configuration } from './config/Configuration';
import { Theme, Theme as ThemeType } from './types/theme';
import { Settings } from './types/settings';
import { State } from './types/state';
import { RequestError, ActivateAccountResponse, LoginResponse } from './types/authentication';
import { User } from './types/user';

enum PopupDisplay {
    None = 0,
    Themes = 1,
    Settings = 2,
    Authentication = 3
}

type ApplicationReactState = {

    /**
     * Object representing the configurations that will take affect
     * on the application.
     */

    settings: Settings;

    /**
     * A state object that is sent from the background service. This
     * will determine the state of the application after it has been
     * initialized.
     */

    state: State;

    /**
     * A list of themes that are available to the user to select from.
     */

    themes: Array<ThemeType>;

    /**
     * An enum representing the current popup that is being rendered
     * on the application.
     */

    currentPopup: PopupDisplay;

    /*
     * State variable to control whether or not to display
     * the goldPromotion popup right away within the settings
     * popup.
    */

    displayGoldPromotion: boolean;

    /**
     * A boolean field to toggle the legal popup.
     */

    didAgree: boolean;

    /**
     * A boolean field to indicate whether or not user is a gold
     * member or not.
     */

    isGold: boolean;

    /**
     * A boolean field to indicate whether or not the user is
     * authenticated.
     */

    user: User | undefined;
};

export default class App extends React.Component<{}, ApplicationReactState> implements QuizzardRuntimeEventListener {

    private static readonly APPLICATION_NAME: string = 'Quizzard';
    private static readonly SUBSCRIPTION_URL: string = 'https://www.quizzardapp.com/quizzard-gold';

    private readonly quizzardRuntime: QuizzardRuntime;

    public input: HTMLInputElement | null;

    constructor(props: any) {

        super(props);

        this.state = App.defaultState;
        this.input = null;

        /*
         * Sets the default state to the last saved state.
         * This way settings are displayed to the user faster.
         */

        BrowserStorage.get(['state', 'settings', 'didAgree', 'isGold', 'user', 'isActivationRequired', 'signInResetTimeEpoch'], this.onLocalApplicationDataLoaded.bind(this));
        this.quizzardRuntime = new QuizzardRuntime(this);
    }

    componentWillMount(): void {

        if (this.input === undefined || this.input === null) {
            return;
        }

        this.input.focus();
    }

    componentWillUnmount(): void {
        this.quizzardRuntime.disconnect();
    }

    onLoadApplicationData(data: { themes: Theme[], isGold: boolean, isActivationRequired: boolean, user?: User }): void {

        // console.log('Application data loaded: ', data);

        let popup: PopupDisplay = this.state.currentPopup;

        if (popup === PopupDisplay.Authentication && data.user !== undefined) {
            popup = PopupDisplay.None;
        }

        /*
         * If the user has turned on gold searching
         * then we should remove any gold promotion
         * popups that may be displayed.
         */

        const isDisplayingGoldPromotionWhenNotSupposedTo: boolean = data.user !== undefined && data.user.isGoldSearch === true && this.state.state.randomizedPromotion !== undefined && this.state.state.randomizedPromotion.promotion.goldPromotion === true;

        if (isDisplayingGoldPromotionWhenNotSupposedTo === true) {
            this.setState({ state: { ...this.state.state, randomizedPromotion: undefined } });
        }

        this.setState({ themes: data.themes, isGold: data.isGold, user: data.user, currentPopup: popup });
    }

    onStateUpdate(state: State): void {

        if (state.reCAPTCHAData !== undefined) {
            this.setState({ state: { ...state } });
            return;
        }

        if (this.input !== undefined && this.input !== null) {
            this.input.value = state.question;
        }

        this.setState({ state });
    }

    private onAuthenticationPopupClosed(user?: User): void {

        if (user !== undefined) {
            this.setState({ user: { email: user.email, userID: user.userID, isGoldSearch: user.isGoldSearch, isFreeTrialActivated: user.isFreeTrialActivated, goldSearchEndTimeEpoch: user.goldSearchEndTimeEpoch } });
        }

        this.quizzardRuntime.resetSignUpSession();
        this.setState({ currentPopup: PopupDisplay.None });
    }

    private onLocalApplicationDataLoaded(data: { state: State, settings: Settings, didAgree: boolean, isGold: boolean, user?: User, isActivationRequired?: boolean, signInResetTimeEpoch?: number }): void {

        if (data.settings === undefined) {
            data.settings = Configuration.DefaultSettings;
        }

        // console.log('Loaded local application data: ', data);

        if (data.settings.autoClear === true && data.state.reCAPTCHAData === undefined) {
            data.state = Configuration.DefaultState;
        }

        if (data.didAgree === undefined) {
            data.didAgree = false;
        }

        if (this.input !== undefined && this.input !== null) {
            this.input.value = data.state.question;
        }

        this.setState({ settings: data.settings, state: data.state, didAgree: data.didAgree, isGold: data.isGold, user: data.user });

        let popup: PopupDisplay = PopupDisplay.None;

        if (data.user === undefined || data.isActivationRequired === true) {

            /*
             * If the amount of time has passed since the user declined
             * to sign in, then we will show the sign in popup again.
             */

            popup = PopupDisplay.Authentication;

            if (data.isActivationRequired === false && data.signInResetTimeEpoch !== undefined && new Date().getTime() <= data.signInResetTimeEpoch) {
                popup = PopupDisplay.None;
            }

        }

        this.setState({ currentPopup: popup });

    }

    private search(question: string): void {

        if (question === undefined) {
            return;
        }

        if (question.trim().length === 0) {
            return;
        }

        if (this.state.state.reCAPTCHAData !== undefined) {
            return;
        }

        this.quizzardRuntime.search(question);
    }

    public onEnterKeyPress(event: any): void {

        if (event.key !== 'Enter') {
            return;
        }

        // console.log('Searching .... key press!');
        this.search(event?.target?.value);
    }

    public onReCAPTCHAResult(result: any): void {

        if (result?.success === false) {
            return;
        }

        const question: string | undefined = this.input?.value;

        if (question === undefined) {
            return;
        }

        const state: State = this.state.state;
        state.reCAPTCHAData = undefined;
        state.isLoading = true;

        this.setState({ state });
        setTimeout(() => this.search(question), 1000);
    }

    public onPaletteClick(): void {
        this.quizzardRuntime.onThemesClick();
        this.setState({ currentPopup: this.state.currentPopup === PopupDisplay.Themes ? PopupDisplay.None : PopupDisplay.Themes });
    }

    public onPaletteCloseClick(): void {
        this.setState({ currentPopup: PopupDisplay.None });
    }

    public onSettingsClick(displayGold?: boolean): void {
        this.quizzardRuntime.onSettingsClick();
        const displayGoldPromotion: boolean = displayGold === true || false;
        this.setState({ displayGoldPromotion, currentPopup: this.state.currentPopup === PopupDisplay.Settings ? PopupDisplay.None : PopupDisplay.Settings });
    }

    public onWebsiteClick(index: number): void {
        this.quizzardRuntime.onWebsiteClick(index);
    }

    public onPromotionClick(id: number): void {
        this.quizzardRuntime.onPromotionClick(id);
    }

    public onSettingsCloseClick(confidenceThreshold: number, autoSort: boolean, autoClear: boolean): void {
        this.quizzardRuntime.updateSearchSettings(confidenceThreshold, autoSort, autoClear);
        this.setState({ currentPopup: PopupDisplay.None, displayGoldPromotion: false });
    }

    public onInputChanged(event: any): void {

        const input = event?.target?.value || undefined;

        if (input === undefined) {
            return;
        }

        const state: State = this.state.state;
        state.question = input;

        this.quizzardRuntime.updateApplicationState(this.state.state);
        this.setState({ state });
    }

    public onAppClick(event: React.MouseEvent<HTMLDivElement, MouseEvent>): void {

        if (event === undefined || event === null) {
            return;
        }

        if ((event.target as any).id === 'btnTheme') {
            return;
        }

        this.setState({ currentPopup: PopupDisplay.None });
    }

    private onThemeChange(theme: ThemeType): void {

        const settings: Settings = this.state.settings;
        settings.windowPrimaryColor = theme.primaryColor;
        settings.windowSecondaryColor = theme.secondaryColor;

        this.setState({ settings });
        this.quizzardRuntime.updateTheme(theme);
    }

    private onClearClick(resetInput: boolean): void {

        const state: State = Configuration.DefaultState;

        if (this.input !== undefined && this.input !== null) {

            state.question = this.input.value;

            if (resetInput === true) {
                this.input.value = '';
                state.question = '';
            }

        }

        this.quizzardRuntime.updateApplicationState(state);
        this.setState({ state });
    }

    public onResetSignUpSession(): void {
        this.quizzardRuntime.resetSignUpSession();
    }

    public onSignupRequest(email: string, password: string, rememberMe: boolean): Promise<RequestError | undefined> {
        return this.quizzardRuntime.signup(email, password, rememberMe);
    }

    public onActivationRequest(email: string, activationCode: string): Promise<ActivateAccountResponse> {
        return this.quizzardRuntime.activate(email, activationCode);
    }

    public onForgotPasswordRequest(email: string): Promise<void> {
        return this.quizzardRuntime.forgotPassword(email);
    }

    public onLoginRequest(email: string, password: string, rememberMe: boolean): Promise<LoginResponse> {
        return this.quizzardRuntime.login(email, password, rememberMe);
    }

    public onLogoutRequest(): void {
        this.quizzardRuntime.logout().finally(() => {
            this.setState({ user: undefined });
        });
    }

    public onSubscribeRequest(): void {

        this.quizzardRuntime.onSubscribeClick();

        if (this.state.user === undefined) {
            this.setState({ currentPopup: PopupDisplay.Authentication });
            return;
        }

        window.open(App.SUBSCRIPTION_URL, '_blank');
    }

    public onFreeTrialRequest(): Promise<{ goldSearchEndTimeEpoch?: number }> {

        this.quizzardRuntime.onFreeTrialClick();

        if (this.state.user === undefined) {
            this.setState({ currentPopup: PopupDisplay.Authentication });
            return Promise.resolve({ goldSearchEndTimeEpoch: undefined });
        }

        return this.quizzardRuntime.activateFreeTrial().then(({ goldSearchEndTimeEpoch }) => {

            if (this.state.user !== undefined && goldSearchEndTimeEpoch !== undefined) {
                const updatedUser: User = { ...this.state.user, isGoldSearch: true, goldSearchEndTimeEpoch };
                this.setState({ user: updatedUser });
            }

            return Promise.resolve({ goldSearchEndTimeEpoch });
        });
    }

    public onDisplayLoginPopup(): void {
        this.setState({ currentPopup: PopupDisplay.Authentication });
    }

    public onIAgreeClick(amountScrolled: number): void {
        this.quizzardRuntime.agreeToTermsOfService(amountScrolled);
        this.setState({ didAgree: true });
    }

    public onDiscordClick(): void {
        this.quizzardRuntime.onDiscordClick();
    }

    public onDonateClick(): void {
        this.quizzardRuntime.onDonateClick();
    }

    public render(): JSX.Element {

        return (

            <div id='App' className='App'>

                <header onClick={this.onAppClick.bind(this)} style={{ backgroundColor: this.state.settings.windowPrimaryColor }} className='App-header'>

                    <div className='App-Header-Wrapper'>

                        <h1 className={this.state?.user?.isGoldSearch && (this.state.user.goldSearchEndTimeEpoch !== undefined && Time.getCurrentTimeEpoch() < this.state.user.goldSearchEndTimeEpoch) ? 'Quizzard-Gold' : ''}>{App.APPLICATION_NAME}</h1>

                        <input
                            onChange={this.onInputChanged.bind(this)}
                            ref={(input) => this.input = input}
                            style={{ backgroundColor: this.state.settings.windowSecondaryColor }}
                            className='search-bar' placeholder='What is the capital of canada?' name='input'
                            onKeyPress={this.onEnterKeyPress.bind(this)}
                        />

                        <button id='btnTheme' className='btn' onClick={this.onPaletteClick.bind(this)}>
                            <img id='btnTheme' src='img/palette.svg' alt='Change Theme' />
                        </button>

                    </div>

                </header>

                <Results
                    reCAPTCHAData={this.state.state.reCAPTCHAData}
                    onReCAPTCHAResult={this.onReCAPTCHAResult.bind(this)}
                    isLoading={this.state.state.isLoading}
                    randomizedPromotion={this.state.state.randomizedPromotion}
                    settings={this.state.settings}
                    results={this.state.state.answers}
                    onClearClick={this.onClearClick.bind(this)}
                    onSettingsClick={this.onSettingsClick.bind(this)}
                    onWebsiteClick={this.onWebsiteClick.bind(this)}
                    onPromotionClick={this.onPromotionClick.bind(this)}
                    question={this.state.state.question}
                    noResults={this.state.state.isLoading === false && this.state.state.noResults === true && this.state.state.question.trim().length !== 0}
                />

                <React.Fragment>

                    <SettingsComponent
                        onLoginOrSignUp={this.onDisplayLoginPopup.bind(this)}
                        onLogout={this.onLogoutRequest.bind(this)}
                        onDiscordClick={this.onDiscordClick.bind(this)}
                        onDonateClick={this.onDonateClick.bind(this)}
                        onFreeTrial={this.onFreeTrialRequest.bind(this)}
                        onSubscribe={this.onSubscribeRequest.bind(this)}
                        settings={this.state.settings}
                        user={this.state.user}
                        display={this.state.currentPopup === PopupDisplay.Settings && this.state.didAgree === true}
                        displayGoldPromotion={this.state.displayGoldPromotion}
                        onClose={this.onSettingsCloseClick.bind(this)}
                    />

                    <Themes
                        themes={this.state.themes}
                        display={this.state.currentPopup === PopupDisplay.Themes && this.state.didAgree === true}
                        selectedColor={this.state.settings.windowPrimaryColor}
                        onThemeChange={this.onThemeChange.bind(this)}
                        onCloseClick={this.onPaletteCloseClick.bind(this)}
                    />

                    <Auth
                        display={this.state.currentPopup === PopupDisplay.Authentication}
                        onResetSignUpSession={this.onResetSignUpSession.bind(this)}
                        onPopupClosed={this.onAuthenticationPopupClosed.bind(this)}
                        onForgotPassword={this.onForgotPasswordRequest.bind(this)}
                        onLogin={this.onLoginRequest.bind(this)}
                        onSignUp={this.onSignupRequest.bind(this)}
                        onActivate={this.onActivationRequest.bind(this)}
                    />

                    <TermsAndConditions
                        display={this.state.currentPopup !== PopupDisplay.Authentication && this.state.didAgree === false}
                        onIAgreeClick={this.onIAgreeClick.bind(this)}
                    />

                </React.Fragment>

            </div>
        );
    }

    private static get defaultState(): ApplicationReactState {
        return {
            settings: Configuration.DefaultSettings,
            state: Configuration.DefaultState,
            themes: Configuration.DefaultThemes,
            currentPopup: PopupDisplay.None,
            displayGoldPromotion: false,
            didAgree: true,
            isGold: false,
            user: undefined
        };
    }

}
